import React from 'react';
import M from 'materialize-css'

export class CtButton extends React.Component {
    render() {
        return (
            <div className="fixed-action-btn">
                <a className="btn-floating btn-large grey darken-4 hoverable pulse modal-trigger" aria-label="Entrar em contato" href="#Contact">
                    <i className="large material-icons">forum</i>
                </a>

            </div>
        );
    }
}

export class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modalInstance: null
        }
    }
    componentDidMount() {
        var elems = document.querySelectorAll('.modal');
        this.setState({ modalInstance: M.Modal.init(elems) });
    }
    componentWillUnmount() {
        this.state.modalInstance.destroy();
    }
    render() {
        return (
            <div id="Contact" className="modal">
                <div className="modal-content">
                    <h4 className="center blue-text">Entre em contato</h4>
                    <div className="row" style={{ marginTop: '2em' }}>
                        <form className="col s12">
                            <div className="row">
                                <div className="input-field col s12 m12 l12 lx12">
                                    <i className="far fa-envelope prefix"></i>
                                    <input id="icon_prefix" type="email" readOnly value="wesley.waaraujo@gmail.com" className="validate" />
                                    <label htmlFor="icon_prefix">Email</label>
                                </div>
                                <div className="input-field col s12 m12 l12 lx12">
                                    <i className="fab fa-whatsapp prefix"></i>
                                    <input id="icon_telephone" type="tel" readOnly value="+55 11 98919 7263" className="validate" />
                                    <label htmlFor="icon_telephone">Celular</label>
                                </div>
                            </div>
                            <small>"Poderia ter um chat né?" Em breve.</small>
                        </form>
                    </div>
                </div>
                <div className="modal-footer">
                    <a href="#!" className="modal-close waves-effect waves-red btn-flat">Fechar</a>
                </div>
            </div>
        );
    }
}