import React from 'react';
import M from 'materialize-css';
import "./Sidenav.css"

export class Sidenav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sidenavInstance: null
        };
        this.onClickLink = this.onClickLink.bind(this);
    }
    componentDidMount() {
        const elem = document.getElementById('MenuMobile');
        this.setState({ sidenavInstance: M.Sidenav.init(elem) })
    }
    componentWillUnmount() {
        this.state.sidenavInstance.destroy();
    }
    onClickLink() {
        this.state.sidenavInstance.close()
    }
    render() {
        return (
            <ul id="MenuMobile" className="sidenav dark">
                <li>
                    <div className="user-view">
                        <div className="background">
                            {/* <img src="/assets/wesley.jpg" alt="" /> */}
                        </div>
                        <a href="##"><img className="circle" aria-label="Favico leydev" src="/assets/icon/favicon.png" alt="" /></a>
                        <a href="#home"><span className="name white-text">Leydev | Wesley Developer</span></a>
                    </div>
                </li>
                <li><a className="" onClick={this.onClickLink} href="https://www.99freelas.com.br/user/wnotmatter">
                    <i class="fas fa-briefcase blue-text"></i> 99 Freelas
                </a></li>
                <li><a className="" onClick={this.onClickLink} href="#whoami">
                    <i className="fas fa-question-circle blue-text"></i>How Am I
                </a></li>
                <li><a className="" onClick={this.onClickLink} href="#services">
                    <i className="fas fa-concierge-bell blue-text"></i>Serviços 
                </a></li>
                <li><a className="" onClick={this.onClickLink} href="#portfolio">
                    <i className="fas fa-bookmark blue-text"></i>Portfólio
                </a></li>
                <li><a className="" onClick={this.onClickLink} href="#certificates">
                    <i className="fas fa-award blue-text"></i>Certificados
                </a></li>
                <li><div className="divider"></div></li>
                <li>
                    <div className="row">
                        <div className="col s6 m6">
                            <a href="//www.linkedin.com/in/wesley-a" target="_blank" rel="noopener noreferrer" className="btn-flat " aria-label="Perfil no linkdin">
                                Linkedin <i className="fab fa-linkedin-in  right"></i>
                            </a>
                        </div>
                        <div className="col s6 m6">
                            <a href="https://github.com/leydev" target="_blank" rel="noopener noreferrer" className="btn-flat " aria-label="Perfil no github">
                                Github <i className="fab fa-github  right"></i>
                            </a>
                        </div>
                    </div>
                </li>
            </ul>
        );
    }
}